import { Component, OnInit } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AngularFireAuth } from '@angular/fire/auth';
import { Formio } from 'angular-formio';
import { AngularFirestore } from '@angular/fire/firestore';
import { LoginComponent } from 'src/app/auth/login/login.component';
declare var window: any;

@Component({
  selector: 'app-create-participant',
  templateUrl: './create-participant.component.html',
  styleUrls: ['./create-participant.component.scss']
})

export class CreateParticipantComponent implements OnInit {
  errorMsg: string = '';
  successMsg: string = '';
  fetching: boolean = true;
  constructor(
    public activeModal: NgbActiveModal,
    public afAuth: AngularFireAuth,
    public afs: AngularFirestore,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.initFormio();
  }

  initFormio() {
    let that = this;
    Formio.createForm(
      document.getElementById('formio'),
      'https://inqforms.mygumpu.com/#/form/5f5c6fc0c6f62176bed9676e/',
      {
        hooks: {
          beforeSubmit: function (submission, next) {
            let data = submission.data;
            ['submit', 'confirmEmail', 'confirmPassword'].forEach(key => {
              delete data[key];
            })
            console.log(next);
            that.afAuth.auth.createUserWithEmailAndPassword(data.email, data.password)
              .then(function (user: any) {
                if (((user || {}).user || {}).uid) {
                  delete data.password;
                  that.afs.collection('participants').doc(user.user.uid).set({ ...data });
                  that.successMsg = 'Participant successfully created please login';
                  setTimeout(() => {
                    that.activeModal.close();
                    that.openLoginModal();
                  }, 2000);
                }
              }).catch(function (error) {
                that.errorMsg = error.message;
              });
          }
        }
      }
    ).then((form) => {
      window.jQuery('.modal-dialog').addClass('modal-dialog modal-lg');
      that.fetching = false;
      form.on('change', function () {
        that.errorMsg = '';
      })
    })
  }

  close() {
    this.activeModal.close();
  }

  openLoginModal() {
    this.modal.open(LoginComponent);
  }
}
