import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { CreateParticipantComponent } from 'src/app/base/components/create-participant/create-participant.component';
import { ForgotPasswordComponent } from '../forgot-password/forgot-password.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AngularFireAuth } from '@angular/fire/auth';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoginComponent implements OnInit {

  successMsg: string = '';
  errorMsg: string = '';
  form: FormGroup;
  option: any = {
    backdrop: 'static',
    keyboard: false
  }

  constructor(
    public modal: NgbModal,
    public activeModal: NgbActiveModal,
    public afAuth: AngularFireAuth,
    public fb: FormBuilder,
    public cd: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    })
  }

  register() {
    this.activeModal.close();
    this.modal.open(CreateParticipantComponent, this.option)
  }

  close() {
    this.activeModal.close();
  }

  forgetPassword() {
    this.activeModal.close();
    this.modal.open(ForgotPasswordComponent, this.option);
  }


  login() {
    if (this.form.invalid) {
      return;
    }
    this.successMsg = '';
    this.errorMsg = '';

    this.afAuth.auth.signInWithEmailAndPassword(this.form.value.email, this.form.value.password)
      .then((res) => {
        localStorage.setItem("participant", JSON.stringify(res));
        this.successMsg = 'Logged in successfully';
        this.cd.detectChanges();
        setTimeout(() => {
          this.activeModal.close(res);
          this.cd.detectChanges();
        }, 3000)
      })
      .catch(e => {
        this.errorMsg = e.message;
        this.cd.detectChanges();
      })
  }
}
